.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
}
.homePage {
  background-color: #555759;
  color: #fff;
  padding-bottom: 4em;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #09d3ac;
}
.contentArea {
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 25px;
  width: 100%;
}
